import React, { Component } from "react";

export default class LandingBanner extends Component {
    render() {
        return (
            <section className="banner-section col-12">
                {/*banner*/}
                <div className="banner">
                    <h1>VietCourses cung cấp các khóa học online đa lĩnh vực</h1>
                    <h2>
                        Chỉ cần vài bước đăng ký tài khoản, bạn có thể học mọi lúc, mọi nơi cùng các giảng viên hàng đầu
                        đến từ các trường đại học trong nước.
                    </h2>
                    <button className="">Đăng ký ngay</button>
                </div>
                <div className="backgr"></div>
                <div className="backgrImg"></div>
            </section>
        );
    }
}
