import React, { useEffect, useState } from "react";
import { CourseSlider } from "../Component/CourseSlider";
import { SubjectSlider } from "../Component/SubjectSlider";
import LoginPopup from "../Component/LoginPopup";
import LandingBanner from "../Component/LandingBanner";
import { useSelector } from "react-redux";
import { useHistory} from "react-router-dom";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import { TopSubject } from "../Component/TopSubject";
import { PartnerLogos } from "../Component/PartnerLogos";

export const LandingPage = (props) => {
    const auth = useSelector((state) => state.userStatus.auth);
    const [listCourse, setListCourse] = useState([]);
    const fetchCourse = async () => {
        const Courses = await Axios.get(`${nodeApiLink}/api/course/get`);
        setListCourse(Courses.data);
    };
    const history = useHistory();
    useEffect(() => {
        if (auth) history.push("/home");
        return () => {
            //
        };
    }, [auth]);
    useEffect(() => {
        fetchCourse();
        return () => {
            //
        };
    }, [auth]);
    return (
        <div>
            <LoginPopup />
            <div className="container-fluid">
                {/*banner*/}
                <LandingBanner />
                <TopSubject/>
                <div className="px-3">
                    <div className="Popular-course">
                        <h3>Khoá học phổ biến</h3>
                        <CourseSlider type="course" courses={listCourse?.length > 0 ? listCourse.slice(0, 11) : []} />
                    </div>
                </div>
                <div className="px-3">
                    <div className="Popular-course">
                        <h3>Khoá học phổ biến</h3>
                        <CourseSlider type="course" courses={listCourse?.length > 0 ? listCourse.slice(11) : []} />
                    </div>
                </div>
                <div className="px-3">
                    <div className="subjects">
                        <h3>Khám phá</h3>
                        <SubjectSlider />
                    </div>
                </div>
            </div>
            <PartnerLogos/>
        </div>
    );
};
export default LandingPage;
