import React, {  useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { signin, loginWithGoogle, loginWithFacebook } from "../action/userAction";
import { googleKey } from "../authKey/googleKey";
import GoogleLogin from "react-google-login";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

export const LoginForm = (props) => {
    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");
    const userLogin = useSelector((state) => state.userStatus);
    const { loading, error } = userLogin;
    const dispatch = useDispatch();

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(signin(userName, password));
    };

    const responseGoogle = (res) => {
        dispatch(loginWithGoogle(res.accessToken));
    };
    const responseFacebook = (res) => {
        dispatch(loginWithFacebook(res.accessToken));
    };
    const useStyle = makeStyles((theme) => ({
        root: {
            "& .MuiTextField-root": {
                margin: theme.spacing(1),
            },
        },
    }));
    const classes = useStyle();
    return (
        <form className={classes.root} onSubmit={(e) => submitHandler(e)}>
            <h1>Đăng nhập</h1>
            <div className="social-container">
                <GoogleLogin
                    clientId={googleKey.googleClientId}
                    render={(renderProps) => (
                        <a className="social" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                            <i className="fab fa-google-plus-g"></i>
                        </a>
                    )}
                    buttonText=""
                    onSuccess={responseGoogle}
                    onFailure={responseGoogle}
                    cookiePolicy={"single_host_origin"}
                />
                <FacebookLogin
                    appId="300653421124122"
                    fields="name,email,picture"
                    render={(renderProps) => (
                        <a className="social" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                            <i className="fab fa-facebook-f"></i>
                        </a>
                    )}
                    textButton=""
                    callback={responseFacebook}
                />
                <a href="#" className="social">
                    <i className="fab fa-linkedin-in"></i>
                </a>
            </div>
            <span>Hoặc sử dụng tài khoản hiện tại</span>
            <TextField
                label="Tên tài khoản"
                onChange={(e) => {
                    setUserName(e.target.value);
                }}
                value={userName}
                fullWidth
                size="small"
            />
            <TextField
                label="Mật khẩu"
                onChange={(e) => {
                    setPassword(e.target.value);
                }}
                value={password}
                type="password"
                fullWidth
            />
            <a href="#">Quên mật khẩu ?</a>
            <button type="submit">Đăng nhập</button>
        </form>
    );
};
