import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { signup, loginWithFacebook, loginWithGoogle } from "../action/userAction";
import GoogleLogin from "react-google-login";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { googleKey } from "../authKey/googleKey";
import { TextField, makeStyles } from "@material-ui/core";

export const SignupForm = (props) => {
    const [name, setName] = useState("");
    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");
    const [rePassword, setRePassword] = useState("");
    const signupUser = useSelector((state) => {
        return state.userStatus;
    });
    const { loading, error } = signupUser;
    const dispatch = useDispatch();
    const responseGoogle = (res) => {
        console.log(res);
        dispatch(loginWithGoogle(res.accessToken));
    };
    const responseFacebook = res => {
        console.log(res)
        dispatch(loginWithFacebook(res.accessToken))
    }
    const onSubmitHandler = (e) => {
        e.preventDefault();
        if (password !== rePassword) {
        } else {
            dispatch(signup(name, userName, password));
        }
    };
    const useStyle = makeStyles((theme) => ({
        root: {
            "& .MuiTextField-root": {
                margin: theme.spacing(1),
            },
        },
    }));
    const classes = useStyle();

    return (
        <form action="#" className={classes.root} onSubmit={(e) => onSubmitHandler(e)}>
            <h1>Đăng ký</h1>
            <div className="social-container">
            <GoogleLogin
                    clientId={googleKey.googleClientId}
                    render={(renderProps) => (
                        <a className="social" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                            <i className="fab fa-google-plus-g"></i>
                        </a>
                    )}
                    buttonText=""
                    onSuccess={responseGoogle}
                    onFailure={responseGoogle}
                    cookiePolicy={"single_host_origin"}
                />
                <FacebookLogin
                    appId="300653421124122"
                    fields="name,email,picture"
                    render={(renderProps) => (
                        <a className="social" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                            <i className="fab fa-facebook-f"></i>
                        </a>
                    )}
                    textButton=''
                    callback={responseFacebook}
                />
                <a href="#" className="social">
                    <i className="fab fa-linkedin-in"></i>
                </a>
            </div>
            <span>Hoặc dùng email để đăng ký tài khoản</span>
            <input type="text" name="name" value={name} onChange={(e) => setName(e.target.value)} placeholder="" />
            <TextField
                label="Tên đăng nhập"
                onChange={(e) => {
                    setUserName(e.target.value);
                }}
                value={userName}
                fullWidth
                size="small"
            />
            <TextField
                label="Mật khẩu"
                onChange={(e) => {
                    setPassword(e.target.value);
                }}
                value={password}
                type="password"
                fullWidth
            />
            <TextField
                label="Nhập lại mật khẩu"
                onChange={(e) => {
                    setRePassword(e.target.value);
                }}
                value={rePassword}
                type="rePassword"
                fullWidth
            />
            <button>Đăng ký</button>
        </form>
    );
};
